@import url('https://fonts.googleapis.com/css2?family=Rubik&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

.rubik {
  font-family: Rubik, sans-serif;
}

.w-100pc {
  width: 100%;
}

.h-100pc {
  height: 100%;
}

.w-75pc {
  width: 75%;
}

.h-75pc {
  height: 75%;
}

.glow {
  filter: drop-shadow(0 0 30px #fff) drop-shadow(0 0 60px #f0f) drop-shadow(0 0 90px #0ff);
}

.animate-ring:hover {
  animation: ring 0.75s;
}

@keyframes ring {
  0%, 100% {
    transform: translateY(0);
    rotate: 0;
  }
  25% {
    rotate: -15deg;
    transform: translateY(-20px);
  }
  50% {
    transform: translateY(0);
  }
  75% {
    rotate: 15deg;
    transform: translateY(-20px);
  }
}